@font-face {
  font-family: 'devroye';
  src: url(/static/media/DEVROYUN.979a2a17.ttf);
  font-weight: normal;
  font-style: normal;
}
body {
  font-family: 'devroye';
}

.gridListTileBarNone > div {
  color: red;
}

.collection-container {
  margin-bottom: 70px;
}

.search-container {
  margin: 10px 122px;
}

.form {
  float: right;
}
.form input {
  font-family: inherit;
  width: 600px;
  padding: 5px 10px;
  font-size: 16px;
  border-radius: 10px;
  border: 1px solid grey;
  display: inline-block;
}
.form input:focus {
  outline: none;
}
.filter {
  display: inline-block;
}
.MuiButtonBase-root-224 {
  font-family: inherit;
}

.clearfix {
  display: block;
}
.clearfix:after {
  content: '';
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.no-found {
  text-align: center;
  margin: 50px auto;
  width: 500px;
}
.pagination {
  width: 600px;
  margin: 40px auto;
  list-style-type: none;
  padding-bottom: 50px;
  text-align: center;
}
.pagination li {
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
}
.pagination li a {
  cursor: pointer;
  outline: none;
}
.pagination li.active {
  border-radius: 5px;
  background-color: #f5f6f4;
}

